// src/utils/VisionAnalyzerWeb.ts
// ==================================================
// Web
// ==================================================

import Tesseract from "tesseract.js";

export async function analyzeImageDetailed(dataUrl: string, paperSize?: string) {
  const img = await new Promise<HTMLImageElement>((resolve, reject) => {
    const im = new Image();
    im.onload = () => resolve(im);
    im.onerror = reject;
    im.src = dataUrl;
  });

  const maxW = 900;
  const scale = img.width > maxW ? maxW / img.width : 1;
  const w = Math.max(1, Math.round(img.width * scale));
  const h = Math.max(1, Math.round(img.height * scale));

  const c = document.createElement("canvas");
  const ctx = c.getContext("2d", { willReadFrequently: true })!;
  c.width = w;
  c.height = h;
  ctx.drawImage(img, 0, 0, w, h);
  const data = ctx.getImageData(0, 0, w, h).data;

  const colorCount = new Map<number, number>();
  let sumLum = 0;
  let warmCount = 0;
  let coolCount = 0;
  let lowContrastPixels = 0;
  let totalPixels = 0;

  const toLum = (r: number, g: number, b: number) => {
    const norm = (v: number) =>
      v <= 0.03928 ? v / 12.92 : Math.pow((v + 0.055) / 1.055, 2.4);
    return (
      0.2126 * norm(r / 255) + 0.7152 * norm(g / 255) + 0.0722 * norm(b / 255)
    );
  };

  const quant = (r: number, g: number, b: number) =>
    ((r >> 5) & 7) * 64 + ((g >> 5) & 7) * 8 + ((b >> 5) & 7);

  // ---  ---
  for (let y = 0; y < h; y++) {
    for (let x = 0; x < w; x++) {
      const i = (y * w + x) * 4;
      const r = data[i], g = data[i + 1], b = data[i + 2];
      const lum = toLum(r, g, b);
      sumLum += lum;

      const hue = Math.atan2(Math.sqrt(3) * (g - b), 2 * r - g - b);
      if (hue > -1 && hue < 1) warmCount++;
      else coolCount++;

      colorCount.set(quant(r, g, b), (colorCount.get(quant(r, g, b)) || 0) + 1);

      if (x + 1 < w) {
        const j = i + 4;
        const diff =
          Math.abs(toLum(r, g, b) - toLum(data[j], data[j + 1], data[j + 2]));
        if (diff < 0.06) lowContrastPixels++;
      }
      totalPixels++;
    }
  }

  const avgLum = sumLum / (w * h);
  const distinctColors = colorCount.size;
  const lowContrastPct = Math.round((lowContrastPixels / totalPixels) * 100);
  const warmRatio = warmCount / (warmCount + coolCount);

  // --- OCR ---
  let avgTextHeight = 0;
  let detectedWords = 0;

  try {
    const ocrResult = await Tesseract.recognize(dataUrl, "jpn+eng");
    const boxes = ((ocrResult.data as any).words || [])
      .map((w: any) => w.bbox)
      .filter((b: any) => b && b.y1 - b.y0 > 0);

    if (boxes.length > 3) {
      avgTextHeight =
        boxes.reduce((sum: number, b: any) => sum + (b.y1 - b.y0), 0) /
        boxes.length;
      detectedWords = boxes.length;
    }
  } catch (err) {
    console.warn("OCR:", err);
  }

  // ---  ---
  const topColors = Array.from(colorCount.entries())
    .sort((a, b) => b[1] - a[1])
    .slice(0, 5)
    .map(([key]) => {
      const rq = (key >> 6) & 7;
      const gq = (key >> 3) & 7;
      const bq = key & 7;
      return `rgb(${Math.round((rq / 7) * 255)},${Math.round(
        (gq / 7) * 255
      )},${Math.round((bq / 7) * 255)})`;
    });

  // --- A/B ---
  const scaleFactorMap: Record<string, number> = {
    A1: 2.8, A2: 2.0, A3: 1.4, A4: 1.0, A5: 0.7,
    B1: 3.0, B2: 2.1, B3: 1.5, B4: 1.1, B5: 0.8
  };
  const printScaleFactor = scaleFactorMap[paperSize ?? "A4"] ?? 1.0;

  // --- Retina / 4K ---
  const pixelRatio = typeof window !== "undefined" ? window.devicePixelRatio || 1 : 1;
  const resolutionFactor = Math.min(1.5, Math.max(0.7, 1 / pixelRatio));

  // ---  ---
  const baseScore =
    100 -
    Math.min(50, lowContrastPct) * 0.85 +
    Math.min(20, distinctColors / 18) -
    Math.abs(avgLum - 0.5) * 45;

  const score =
    Math.max(0, Math.min(100, Math.round(baseScore * resolutionFactor * printScaleFactor)));

  // --- AI ---
  const advice: string[] = [];

  if (lowContrastPct > 60) {
    advice.push("");
  } else if (lowContrastPct > 40) {
    advice.push("");
  } else {
    advice.push("");
  }

  if (avgLum < 0.3) {
    advice.push("");
  } else if (avgLum > 0.85) {
    advice.push("");
  } else if (avgLum > 0.65) {
    advice.push("");
  } else {
    advice.push("");
  }

  if (distinctColors > 200) {
    advice.push("");
  } else if (distinctColors > 120) {
    advice.push("");
  } else {
    advice.push("");
  }

  if (avgTextHeight > 40) {
    advice.push("");
  } else if (avgTextHeight > 25) {
    advice.push("");
  } else if (avgTextHeight > 10) {
    advice.push("");
  }

  advice.push(` ${topColors.join(", ")} `);
  advice.push(`${paperSize ?? "A4"}  ${resolutionFactor.toFixed(2)}`);

  // ---  ---
  return {
    score,
    avgLum: Number(avgLum.toFixed(3)),
    distinctColors,
    lowContrastPct,
    topColors,
    advice,
  };
}