// src/components/ColorSimulationView.tsx

import React, { useEffect, useRef } from "react";
import { simulateColorVision } from "../utils/ColorSimulation";
import type { ColorVisionType } from "../utils/ColorSimulation";

type Props = {
  imageDataUrl: string;
  type: ColorVisionType;
  agingIntensity: number; // 0100
  blurIntensity: number;  // 08
};

const ColorSimulationView: React.FC<Props> = ({
  imageDataUrl,
  type,
  agingIntensity,
  blurIntensity,
}) => {
  const canvasRef = useRef<HTMLCanvasElement | null>(null);

  useEffect(() => {
    if (!imageDataUrl) return;

    const img = new Image();
    img.crossOrigin = "anonymous";
    img.src = imageDataUrl;

    img.onload = () => {
      const canvas = canvasRef.current;
      if (!canvas) return;
      const ctx = canvas.getContext("2d");
      if (!ctx) return;

      const w = img.width;
      const h = img.height;
      if (!w || !h) return;

      canvas.width = w;
      canvas.height = h;

      // 
      ctx.clearRect(0, 0, w, h);
      ctx.drawImage(img, 0, 0, w, h);

      // 
      if (type === "normal") {
        return;
      }

      // P/D/T
      if (type === "protan" || type === "deutan" || type === "tritan") {
        const imageData = ctx.getImageData(0, 0, w, h);
        const simulated = simulateColorVision(imageData, type);
        ctx.putImageData(simulated, 0, 0);
        return;
      }

      // AGING
      if (type === "aging") {
        const imageData = ctx.getImageData(0, 0, w, h);
        const data = imageData.data;
        const k = Math.max(0, Math.min(agingIntensity, 100)) / 100; // 01

        // 
        for (let i = 0; i < data.length; i += 4) {
          let r = data[i];
          let g = data[i + 1];
          let b = data[i + 2];

          // 
          r = r + 40 * k;
          g = g + 25 * k;
          b = b * (1 - 0.6 * k);

          // 
          const brightness = 1 - 0.25 * k;
          r *= brightness;
          g *= brightness;
          b *= brightness;

          // clamp
          data[i]     = r < 0 ? 0 : r > 255 ? 255 : r;
          data[i + 1] = g < 0 ? 0 : g > 255 ? 255 : g;
          data[i + 2] = b < 0 ? 0 : b > 255 ? 255 : b;
        }

        ctx.putImageData(imageData, 0, 0);
        return;
      }
    };
  }, [imageDataUrl, type, agingIntensity]);

  //  CSS filter 
  const blurStyle =
    blurIntensity > 0
      ? `blur(${Math.max(0, Math.min(blurIntensity, 8))}px)`
      : "none";

  return (
    <div style={{ marginTop: 12 }}>
      <canvas
        ref={canvasRef}
        style={{
          maxWidth: "100%",
          borderRadius: 12,
          boxShadow: "0 2px 6px rgba(0,0,0,.2)",
          filter: blurStyle,
          transition: "filter 0.15s linear",
        }}
      />
    </div>
  );
};

export default ColorSimulationView;