import html2canvas from "html2canvas";
import jsPDF from "jspdf";

type PdfReportProps = {
  imageDataUrl: string;
  contrast: { ratio: number; level: string; comment: string };
  readability: { score: number; comment: string };
  adviceList: string[];
  printSize?: string; // 
};

export async function generatePdfReport(props: PdfReportProps): Promise<Blob> {
  const { imageDataUrl, contrast, readability, adviceList, printSize } = props;

  //  DOMPDF
  const container = document.createElement("div");
  container.style.width = "794px"; // A4(px)
  container.style.minHeight = "1123px"; // A4(px)
  container.style.padding = "40px";
  container.style.background = "#fff";
  container.style.color = "#000";
  container.style.fontFamily = "'Hiragino Kaku Gothic ProN', 'Noto Sans JP', sans-serif";

  container.innerHTML = `
    <div style="text-align:center; margin-bottom:24px;">
      <img src="${process.env.PUBLIC_URL}/Icon-1024pt@1x.png" width="72" height="72" />
      <h1 style="margin:12px 0 4px; font-size:20px;"> </h1>
      <div style="font-size:14px;">Web</div>
    </div>

    <div style="text-align:left; font-size:13px; line-height:1.7;">
      <p><b> </b>${printSize || "A4"}</p>
      <p><b></b>${contrast.comment}${contrast.ratio}:1 / ${contrast.level}</p>
      <p><b></b>${readability.score}</p>
      <p>${readability.comment}</p>
    </div>

    <div style="margin:28px 0;">
      <img src="${imageDataUrl}" style="max-width:100%; border-radius:10px; box-shadow:0 2px 10px rgba(0,0,0,.15);" />
    </div>

    <div style="font-size:13px; text-align:left; line-height:1.8;">
      <b> AI</b>
      <ul>
        ${adviceList.map((a) => `<li>${a}</li>`).join("")}
      </ul>
    </div>

    <div style="text-align:right; font-size:10px; margin-top:30px; color:#555;">
      Barrier-Free Design Checker  ${new Date().getFullYear()}
    </div>
  `;

  document.body.appendChild(container);

  //  Canvas2
  const canvas = await html2canvas(container, {
    scale: 2,
    useCORS: true,
    backgroundColor: "#ffffff",
  });

  //  Canvas
  const imgData = canvas.toDataURL("image/jpeg", 1.0);
  const pdf = new jsPDF({ orientation: "portrait", unit: "pt", format: "a4" });

  const pageWidth = pdf.internal.pageSize.getWidth();
  const pageHeight = pdf.internal.pageSize.getHeight();

  //  
  const imgWidth = pageWidth - 40;
  const imgHeight = (canvas.height * imgWidth) / canvas.width;

  let yPos = 20;
  if (imgHeight < pageHeight - 40) {
    yPos = (pageHeight - imgHeight) / 2; // 
  } else if (imgHeight > pageHeight - 20) {
    // 
    const scale = (pageHeight - 40) / imgHeight;
    pdf.addImage(imgData, "JPEG", 20, 20, imgWidth * scale, imgHeight * scale);
    document.body.removeChild(container);
    return pdf.output("blob");
  }

  pdf.addImage(imgData, "JPEG", 20, yPos, imgWidth, imgHeight);
  document.body.removeChild(container);

  return pdf.output("blob");
}