import React, { useCallback, useState } from "react";

type Props = {
  onDataUrl: (dataUrl: string) => void;
  onSelect: () => void;
};

const ImageUploader: React.FC<Props> = ({ onDataUrl, onSelect }) => {
  const [dragOver, setDragOver] = useState(false);

  // 
  const handleFile = useCallback(
    (file: File) => {
      const reader = new FileReader();
      reader.onload = (e) => {
        const result = e.target?.result as string;
        if (result) {
          onDataUrl(result);
          onSelect();
        }
      };
      reader.readAsDataURL(file);
    },
    [onDataUrl, onSelect]
  );

  // 
  const handleDrop = useCallback(
    (e: React.DragEvent<HTMLDivElement>) => {
      e.preventDefault();
      setDragOver(false);
      const file = e.dataTransfer.files[0];
      if (file && (file.type === "image/jpeg" || file.type === "image/png")) {
        handleFile(file);
      } else {
        alert(" JPG  PNG ");
      }
    },
    [handleFile]
  );

  // 
  const handleInput = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      const file = e.target.files?.[0];
      if (file && (file.type === "image/jpeg" || file.type === "image/png")) {
        handleFile(file);
      } else {
        alert(" JPG  PNG ");
      }
    },
    [handleFile]
  );

  return (
    <div
      onDragOver={(e) => {
        e.preventDefault();
        setDragOver(true);
      }}
      onDragLeave={() => setDragOver(false)}
      onDrop={handleDrop}
      style={{
        border: dragOver ? "3px dashed #43A047" : "3px dashed #bbb",
        borderRadius: 12,
        padding: "40px 20px",
        textAlign: "center",
        background: dragOver ? "#E8F5E9" : "#fff",
        color: "#555",
        transition: "all 0.2s ease",
        cursor: "pointer",
        boxShadow: "0 2px 6px rgba(0,0,0,0.1)",
      }}
    >
      <p style={{ fontSize: "1.1rem", fontWeight: "bold", marginBottom: 8 }}>
        
      </p>
      <p style={{ fontSize: "0.95rem", color: "#666", lineHeight: 1.6 }}>
        <br />
        <strong style={{ color: "#2e7d32" }}>JPGPNG</strong>
      </p>

      <input
        type="file"
        accept="image/jpeg,image/png"
        onChange={handleInput}
        style={{ display: "none" }}
        id="uploader-input"
      />

      <label
        htmlFor="uploader-input"
        style={{
          display: "inline-block",
          marginTop: 14,
          background: "linear-gradient(180deg,#66BB6A,#43A047)",
          color: "#fff",
          padding: "10px 24px",
          borderRadius: 8,
          fontWeight: "bold",
          cursor: "pointer",
          boxShadow: "0 3px 6px rgba(0,0,0,0.2)",
          transition: "background 0.2s",
        }}
      >
        
      </label>
    </div>
  );
};

export default ImageUploader;