import React, { useMemo, useState } from "react";
import ImageUploader from "./components/ImageUploader";
import ColorSimulationView from "./components/ColorSimulationView";
import { generatePdfReport } from "./utils/pdfGenerator";
import { analyzeImageDetailed } from "./utils/VisionAnalyzerWeb"; //  
import "./App.css";

type VisionType = "normal" | "protan" | "deutan" | "tritan" | "aging";
type InputMode = "image" | "web";
type PaperSize =
  | "A1"
  | "A2"
  | "A3"
  | "A4"
  | "A5"
  | "B1"
  | "B2"
  | "B3"
  | "B4"
  | "B5";

function App() {
  const [inputMode, setInputMode] = useState<InputMode>("image");
  const [dataUrl, setDataUrl] = useState<string | null>(null);
  const [url, setUrl] = useState("");
  const [visionType, setVisionType] = useState<VisionType>("normal");
  const [agingIntensity, setAgingIntensity] = useState(0);
  const [blurIntensity, setBlurIntensity] = useState(0);
  const [analysis, setAnalysis] = useState<string>("");
  const [advice, setAdvice] = useState<string[]>([]);
  const [score, setScore] = useState<number | null>(null);
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [paperSize, setPaperSize] = useState<PaperSize>("A4"); //  

  // ---------- AI ----------
  const handleAnalyze = async () => {
    if (!dataUrl && !url) return;
    setIsAnalyzing(true);
    setAnalysis("AI");

    let targetImage = dataUrl;

    // Web
    if (inputMode === "web" && url) {
      try {
        const res = await fetch(
          `https://image.thum.io/get/width/1200/crop/800/noanimate/${encodeURIComponent(url)}`
        );
        const blob = await res.blob();
        targetImage = URL.createObjectURL(blob);
      } catch {
        setAnalysis("CORS");
        setIsAnalyzing(false);
        return;
      }
    }

    if (targetImage) {
      const result = await analyzeImageDetailed(targetImage, paperSize); //  
      setScore(result.score);
      setAdvice(result.advice);
      setAnalysis(
        `\n\n${result.score}\n${result.avgLum}\n${result.distinctColors}\n${result.lowContrastPct}%`
      );
    }

    setIsAnalyzing(false);
  };

  // ---------- PDF ----------
  const handlePdfExport = async () => {
    if (!dataUrl) return;

    const blob = await generatePdfReport({
      imageDataUrl: dataUrl,
      contrast: { ratio: 4.5, level: "AA", comment: "" },
      readability: { score: score ?? 80, comment: "" },
      adviceList: advice,
      printSize: paperSize, //  
    });

    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = `-${paperSize}.pdf`;
    a.click();
  };

  // ----------  ----------
  const preview = useMemo(
    () =>
      dataUrl ? (
        <img
          src={dataUrl}
          alt="preview"
          style={{
            maxWidth: "100%",
            height: "auto",
            borderRadius: 10,
            boxShadow: "0 2px 8px rgba(0,0,0,.15)",
          }}
        />
      ) : null,
    [dataUrl]
  );

  // ---------- UI ----------
  return (
    <div
      style={{
        background: "#f4f7f6",
        minHeight: "100vh",
        padding: "36px 20px",
        textAlign: "center",
      }}
    >
      <header style={{ marginBottom: 24 }}>
        <img
          src={`${process.env.PUBLIC_URL}/Icon-1024pt@1x.png`}
          alt="BarrierFreeChecker Logo"
          style={{ width: 84, height: 84, marginBottom: 8 }}
        />
        <h1>Web</h1>
      </header>

      {/*  */}
      <div style={{ marginBottom: 20 }}>
        <button
          onClick={() => setInputMode("image")}
          style={{
            marginRight: 8,
            padding: "8px 16px",
            borderRadius: 8,
            border:
              inputMode === "image" ? "2px solid #43A047" : "1px solid #ccc",
            background: inputMode === "image" ? "#E8F5E9" : "#fff",
            fontWeight: "bold",
          }}
        >
          
        </button>
        <button
          onClick={() => setInputMode("web")}
          style={{
            padding: "8px 16px",
            borderRadius: 8,
            border:
              inputMode === "web" ? "2px solid #43A047" : "1px solid #ccc",
            background: inputMode === "web" ? "#E8F5E9" : "#fff",
            fontWeight: "bold",
          }}
        >
          WebURL
        </button>
      </div>

      <div style={{ maxWidth: 900, margin: "0 auto" }}>
        {inputMode === "image" ? (
          <ImageUploader onSelect={() => {}} onDataUrl={setDataUrl} />
        ) : (
          <div>
            <input
              type="text"
              placeholder="https://example.com"
              value={url}
              onChange={(e) => setUrl(e.target.value)}
              style={{
                width: "80%",
                padding: "8px",
                borderRadius: 6,
                border: "1px solid #ccc",
              }}
            />
          </div>
        )}

        {/*  */}
        {dataUrl && inputMode === "image" && (
          <div
            style={{
              background: "#fff",
              borderRadius: 12,
              padding: 16,
              marginTop: 16,
            }}
          >
            {preview}
          </div>
        )}

        {/*  */}
        <div style={{ marginTop: 20 }}>
          <label></label>
          <select
            value={paperSize}
            onChange={(e) => setPaperSize(e.target.value as PaperSize)}
            style={{ padding: "6px", borderRadius: 6 }}
          >
            {[
              "A1",
              "A2",
              "A3",
              "A4",
              "A5",
              "B1",
              "B2",
              "B3",
              "B4",
              "B5",
            ].map((s) => (
              <option key={s} value={s}>
                {s}
              </option>
            ))}
          </select>
        </div>

        {/*  */}
        <button
          onClick={handleAnalyze}
          disabled={isAnalyzing || (!dataUrl && !url)}
          style={{
            marginTop: 20,
            background: "linear-gradient(180deg,#66BB6A,#43A047)",
            color: "#fff",
            border: "none",
            borderRadius: 10,
            boxShadow: "0 4px 10px rgba(0,0,0,.22)",
            padding: "12px 26px",
            fontSize: "1rem",
            cursor: "pointer",
            fontWeight: "bold",
          }}
        >
          {isAnalyzing ? "" : ""}
        </button>

        {/*  */}
        {analysis && (
          <div
            style={{
              background: "#fff",
              borderRadius: 12,
              padding: 20,
              marginTop: 20,
              textAlign: "left",
            }}
          >
            <h3></h3>
            <pre style={{ whiteSpace: "pre-wrap" }}>{analysis}</pre>
            {advice.length > 0 && (
              <>
                <h4>AI</h4>
                <ul>
                  {advice.map((a, i) => (
                    <li key={i}>{a}</li>
                  ))}
                </ul>
              </>
            )}
          </div>
        )}

        {/*  */}
        {dataUrl && (
          <section
            style={{
              background: "#fff",
              borderRadius: 12,
              padding: 20,
              marginTop: 20,
            }}
          >
            <h3></h3>
            <div style={{ marginBottom: 12 }}>
              {(
                ["normal", "protan", "deutan", "tritan", "aging"] as VisionType[]
              ).map((mode) => (
                <button
                  key={mode}
                  onClick={() => setVisionType(mode)}
                  style={{
                    margin: "0 6px",
                    padding: "6px 18px",
                    borderRadius: 10,
                    border:
                      visionType === mode
                        ? "2px solid #4CAF50"
                        : "1px solid #ccc",
                    background:
                      visionType === mode
                        ? "linear-gradient(180deg,#A5D6A7,#81C784)"
                        : "#fff",
                    fontWeight: "bold",
                    cursor: "pointer",
                  }}
                >
                  {mode === "normal"
                    ? ""
                    : mode === "protan"
                    ? "P"
                    : mode === "deutan"
                    ? "D"
                    : mode === "tritan"
                    ? "T"
                    : ""}
                </button>
              ))}
            </div>

            {visionType === "aging" && (
              <div style={{ marginBottom: 16 }}>
                <label>{agingIntensity}%</label>
                <input
                  type="range"
                  min={0}
                  max={100}
                  step={1}
                  value={agingIntensity}
                  onChange={(e) => setAgingIntensity(Number(e.target.value))}
                  style={{ width: "60%" }}
                />
                <label style={{ display: "block", marginTop: 10 }}>
                  {blurIntensity}px
                </label>
                <input
                  type="range"
                  min={0}
                  max={8}
                  step={0.5}
                  value={blurIntensity}
                  onChange={(e) =>
                    setBlurIntensity(Number(e.target.value))
                  }
                  style={{ width: "60%" }}
                />
              </div>
            )}

            <ColorSimulationView
              imageDataUrl={dataUrl!}
              type={visionType}
              agingIntensity={agingIntensity}
              blurIntensity={blurIntensity}
            />
          </section>
        )}

        {/* PDF */}
        {dataUrl && (
          <button
            onClick={handlePdfExport}
            style={{
              background: "linear-gradient(180deg,#66BB6A,#43A047)",
              color: "#fff",
              border: "none",
              borderRadius: 10,
              boxShadow: "0 4px 10px rgba(0,0,0,.22)",
              padding: "12px 26px",
              marginTop: 24,
              fontWeight: "bold",
              cursor: "pointer",
            }}
          >
            PDF
          </button>
        )}
      </div>
    </div>
  );
}

export default App;